.product_container {
    Max-width: 1296px;
    text-align: center;
    margin: 0 auto;


    .function {
        display: flex;
        justify-content: center;

        h2 {
            font-size: 32px;
            line-height: 48px;
            border-left: 1px solid #DCDCDC;
            border-right: 1px solid #DCDCDC;
            width: 100%;

            &:nth-child(1) {
                a {
                    border-bottom: 8px solid #AA0601;
                }
            }

            a {
                padding: 24px 0;
                color: #000000;
                display: block;

                &:hover {
                    background-color: #AA0601;
                    border-bottom: 8px solid #AA0601;
                }
            }
        }
    }
}

.picture {
    display: flex;
    padding-bottom: 80px;
}

.picture1 {
    background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-1.png?raw=true);
    background-position: center;
    background-size: cover;
    width: 50%;
    height: 296px;
}

.picture2 {
    background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-2.png?raw=true);
    background-position: center;
    background-size: cover;
    width: 50%;
    height: 296px;
}

.product {
    text-align: center;

    h2 {
        font-size: 42px;
        line-height: 63px;
    }

    h3 {
        font-size: 32px;
        line-height: 48px;
        padding-bottom: 32px;
    }

    .glasses {
        display: flex;
        flex-wrap: wrap;

        li {
            width: 25%;
            padding: 0 12px;
            padding-bottom: 80px;


            img {
                width: 100%;
            }

            .productInfo {
                display: flex;
                justify-content: space-between;

                p {
                    font-size: 24px;
                    line-height: 36px;

                    &:nth-child(2) {
                        color: #AA0601;
                    }
                }
            }

            .color {
                display: flex;
            }

            .color1 {
                width: 24px;
                height: 24px;
                background-color: #5F3E2D;
                border-radius: 4px;
                margin-right: 8px;
            }

            .color2 {
                width: 24px;
                height: 24px;
                background-color: #B75929;
                border-radius: 4px;
            }
        }
    }
}

.page {
    display: flex;
    justify-content: center;
    padding-bottom: 80px;

    p {
        border: 1px solid #555555;

        &:nth-child(1) {
            a {
                padding: 11px 12px;
                font-size: 19px;
                line-height: 26px;
            }
        }

        &:nth-child(3) {
            background-color: #000000;

            a {
                color: #FFFFFF;
            }
        }

        &:nth-child(7) {
            a {
                padding: 11px 12px;
                font-size: 19px;
                line-height: 26px;
            }
        }

        a {
            display: block;
            color: #555555;
            padding: 12px 16px;
            font-size: 18px;
            line-height: 24px;

            &:hover {
                background-color: #000000;
                color: #FFFFFF;
            }
        }
    }
}

@media (max-width: 992px) {
    .product_container {
        .function {
            h2 {
                a {
                    padding: 16px 0;
                }
            }
        }
    }

    .product {
        .glasses {
            padding: 0 24px;

            li {
                padding-bottom: 40px;
                width: 50%;
            }
        }
    }

    .page {
        padding-top: 40px;
    }
}

@media (max-width: 767px) {
    .product_container {
        .function {
            h2 {
                font-size: 24px;
                line-height: 36px;
            }

            h3 {
                font-size: 24px;
                line-height: 36px;
            }
        }
    }

    .picture {
        padding-bottom: 40px;
    }

    .picture1 {
        background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-sm-1.png?raw=true);
    }

    .picture2 {
        background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-sm-2.png?raw=true);
    }

    .product {
        h2 {
            font-size: 24px;
            line-height: 36px;
        }

        h3 {
            font-size: 24px;
            line-height: 36px;
            padding-bottom: 24px;
        }

        .glasses {
            padding: 0;

            li {
                padding-bottom: 24px;
                width: 100%;
            }
        }
    }

    .page {
        p {
            font-size: 16px;
            line-height: 22px;
        }

        padding-bottom: 64px;
    }

}